/**
 * List resets and defaults.
 */
ul,
ol {
  margin: 0;
  padding: 0;
}

ul[class]:not(.ul),
ol[class]:not(.ol) {
  list-style: none;
}

ul:not([class]),
ol:not([class]),
.ul,
.ol {
  padding-left: 24px;
}
:not(li) > ul:not([class]):not(:last-child),
:not(li) > ol:not([class]):not(:last-child),
:not(li) > .ul:not(:last-child),
:not(li) > .ol:not(:last-child) {
  margin-bottom: 16px;
}